import React, { useState } from "react";
import Accordion from "@mui/material/Accordion";
import AccordionSummary from "@mui/material/AccordionSummary";
import AccordionDetails from "@mui/material/AccordionDetails";
import Typography from "@mui/material/Typography";
import "./Accordion.css";
import Card from "../Card";
import { Grid } from "@mui/material";

const AccordionAbout = props => {
  const data = props.data;
  const [expandedTeam, setExpandedTeam] = useState("panelTteam1");
  const setExpandedAccordion = props.setExpandedAccordion;
  const expandedAccordion = props.expandedAccordion;
  console.log("team", Object.values(data.teamDesc));
  const handleChange = panel => (event, newExpanded) => {
    setExpandedAccordion(newExpanded ? panel : false);
  };
  const handleChangeTeam = panel => (event, newExpanded) => {
    setExpandedTeam(newExpanded ? panel : false);
  };

  return (
    <div className="accordionWrapper">
      <Accordion
        expanded={expandedAccordion === "history"}
        onChange={handleChange("history")}
      >
        <AccordionSummary aria-controls="panel1d-content" id="panel1d-header">
          <Typography className="accordionTitle " variant="h4">
            {data.historyCompanyTitle}
          </Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography variant="body1" align="justify">
            {data.historyCompanyDesc}
          </Typography>
        </AccordionDetails>
      </Accordion>
      <Accordion
        expanded={expandedAccordion === "mission"}
        onChange={handleChange("mission")}
      >
        <AccordionSummary aria-controls="panel2d-content" id="panel2d-header">
          <Typography className="accordionTitle " variant="h4">
            {data.missionTitle}
          </Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography variant="body1" align="justify">
            {data.missionDesc}
          </Typography>
        </AccordionDetails>
      </Accordion>
      <Accordion
        expanded={expandedAccordion === "team"}
        onChange={handleChange("team")}
      >
        <AccordionSummary aria-controls="panel3d-content" id="panel3d-header">
          <Typography className="accordionTitle" variant="h4">
            {data.teamTitle}
          </Typography>
        </AccordionSummary>
        <AccordionDetails>
          {Object.values(data.teamDesc).map((el, indx) => (
            <Accordion
              expanded={expandedTeam === `panelTeam${indx + 1}`}
              onChange={handleChangeTeam(`panelTeam${indx + 1}`)}
            >
              <AccordionSummary
                aria-controls={`panel${indx + 1}dTeam-content`}
                id={`panel${indx + 1}dTeam-content`}
              >
                <Typography className="accordionTitle " variant="h4">
                  {el.nameDesc}
                </Typography>
              </AccordionSummary>
              <AccordionDetails>
                <Grid container>
                  {el.photo.map(elm => (
                    <Grid xs={4} item>
                      <Card el={elm} />
                    </Grid>
                  ))}
                </Grid>
              </AccordionDetails>
            </Accordion>
          ))}
        </AccordionDetails>
      </Accordion>
      <Accordion
        expanded={expandedAccordion === "policyOfQuality"}
        onChange={handleChange("policyOfQuality")}
      >
        <AccordionSummary aria-controls="panel4d-content" id="panel4d-header">
          <Typography className="accordionTitle " variant="h4">
            {data.qualityPolicyTitle}
          </Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography variant="body1" align="justify">
            {data.qualityPolicyDesc}
          </Typography>
        </AccordionDetails>
      </Accordion>
      <Accordion
        expanded={expandedAccordion === "legalInformation"}
        onChange={handleChange("legalInformation")}
      >
        <AccordionSummary aria-controls="panel5d-content" id="panel5d-header">
          <Typography className="accordionTitle " variant="h4">
            {data.legalInformationTitle}
          </Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Grid container>
            {data.legalInformationDesc.map(elm => (
              <Grid xs={6} item>
                <img src={elm} className="legal" alt="bla" />
              </Grid>
            ))}
          </Grid>
        </AccordionDetails>
      </Accordion>
    </div>
  );
};
export default AccordionAbout;
