import React from "react";
import { Box, Button, Grid, Typography } from "@mui/material";
import { Link } from "react-router-dom";
import "./AboutUsHome.css";

const AboutUsHome = props => {
  const data = props.data;
  const activeMenuItem = props.activeMenuItem;
  const setActiveMenuItem = props.setActiveMenuItem;
  const expandedAccordion = props.expandedAccordion;
  const setExpandedAccordion = props.setExpandedAccordion;

  return (
    <>
      <Box sx={{ flexGrow: 1 }}>
        <Grid container spacing={2} sx={{ margin: "0px", width: "100%" }}>
          <Grid
            xs={4}
            sx={{ flexBasis: "31.5% !important", maxWidth: "31.5%" }}
          >
            <img className="imgBlock" src={data.aboutUsPhotoUrl} alt="Photo" />
          </Grid>
          <Grid
            xs={8}
            sx={{
              flexBasis: "68.5% !important",
              maxWidth: "68.5% !important",
              background: "#CC9753"
            }}
          >
            <Box sx={{ padding: "4rem 8rem", color: "#fff" }}>
              <Typography variant="h4">{data.aboutUsTitle}</Typography>
              <Typography variant="h6">{data.aboutUsDesc}</Typography>
              <Button
                variant="contained"
                size="small"
                sx={{
                  background: "#fff",
                  color: "#092f63",
                  textTransform: "uppercase"
                }}
              >
                {data.aboutUsButton}
              </Button>
            </Box>
          </Grid>
        </Grid>
        <div className="margin"></div>
        <Grid
          container
          direction="row"
          justifyContent="center"
          alignItems="center"
          className="iconBlock"
        >
          {data.aboutUsIcons.map(el => (
            <Link
              to={`/aboutUs`}
              onClick={() => {
                setActiveMenuItem("aboutUs");
                setExpandedAccordion(`${el.name}`);
              }}
              className="iconLink"
            >
              <Grid item xs={2} className="iconImgBlock">
                <div className="iconBlock">
                  <img src={el.url} className="iconImg" alt={el.name} />

                  <Typography
                    variant="body1"
                    align="center"
                    className="iconText"
                  >
                    {el.text}
                  </Typography>
                </div>
              </Grid>
            </Link>
          ))}
        </Grid>
        <div className="margin"></div>
      </Box>
    </>
  );
};
export default AboutUsHome;
