import React, { useState } from "react";
import { Link } from "react-router-dom";
import "./AppHeader.css";
import {
  AppBar,
  Container,
  Toolbar,
  Typography,
  Box,
  IconButton,
  MenuItem,
  Menu,
  Button,
  Tooltip,
  FormControl,
  InputLabel,
  Select
} from "@mui/material";
import logo from "../../img/woodec-logo.svg";
import Icon from "@mui/icons-material/ExpandMore";
import { createTheme, ThemeProvider } from "@mui/material/styles";

import AppMenu from "../AppMenu";
const theme = createTheme({});

const pages = ["Products", "Pricing", "Blog"];
const settings = ["Profile", "Account", "Dashboard", "Logout"];
const Dropdown = props => {
  const setLang = props.setLang;
  const langs = props.langs;

  if (props.langDrop === false) {
    return null;
  } else {
    return (
      <div
        className="dropdown-menu visibled"
        aria-labelledby="navbarDropdownMenuLink"
      >
        {langs.map(el => {
          return (
            <p
              className="dropdown-item"
              onClick={() => {
                setLang(el);
                props.setLangDrop(false);
                props.setMobileMenu("");
              }}
              key={`lang-${el}`}
            >
              {el}
            </p>
          );
        })}
      </div>
    );
  }
};

const AppHeader = props => {
  const lang = props.lang;
  const data = props.data;
  const setLang = props.setLang;
  const langs = props.langs;
  const setActiveMenuItem = props.setActiveMenuItem;
  const activeMenuItem = props.activeMenuItem;
  const setExpandedAccordion = props.setExpandedAccordion;
  const [langDrop, setLangDrop] = useState(false);
  const [mobileMenu, setMobileMenu] = useState("");
  const [anchorElNav, setAnchorElNav] = React.useState(null);
  const [anchorElUser, setAnchorElUser] = React.useState(null);
  const handleChange = e => {
    setLang(e.target.value);
  };

  const handleOpenNavMenu = event => {
    setAnchorElNav(event.currentTarget);
  };
  const handleOpenUserMenu = event => {
    setAnchorElUser(event.currentTarget);
  };

  const handleCloseNavMenu = () => {
    setAnchorElNav(null);
  };

  const handleCloseUserMenu = () => {
    setAnchorElUser(null);
  };
  return (
    <ThemeProvider theme={theme}>
      <AppBar
        position="sticky"
        sx={{ background: "#F1F2F5", boxShadow: "none" }}
      >
        <Toolbar
          disableGutters
          sx={{
            minHeight: "15rem !important"
          }}
        >
          <Box
            sx={{
              display: "flex",
              flexBasis: "31.5%",
              maxWidth: "31.5%",
              background: "#092f63",
              height: "15rem",
              alignItems: "center",
              justifyContent: "center"
            }}
          >
            <Link to={`/`} onClick={() => setActiveMenuItem("/")}>
              <img src={logo} className="img" alt="WOODEC" />
            </Link>
          </Box>
          {/*--------*/}
          <Box
            sx={{
              flexGrow: 1,
              display: { xs: "flex", md: "none" }
            }}
          >
            <Menu
              id="menu-appbar"
              anchorEl={anchorElNav}
              anchorOrigin={{
                vertical: "bottom",
                horizontal: "left"
              }}
              keepMounted
              transformOrigin={{
                vertical: "top",
                horizontal: "left"
              }}
              open={Boolean(anchorElNav)}
              onClose={handleCloseNavMenu}
              sx={{
                display: { xs: "block", md: "none" }
              }}
            >
              {pages.map(page => (
                <MenuItem key={page} onClick={handleCloseNavMenu}>
                  <Typography textAlign="center">{page}</Typography>
                </MenuItem>
              ))}
            </Menu>
          </Box>
          {/*---------*/}
          <Box
            sx={{
              flexGrow: 1,
              flexBasis: "63.5%",
              maxWidth: "63.5%",
              display: { xs: "none", md: "flex" },
              justifyContent: "center"
            }}
          >
            {Object.keys(data).map((key, indx) => {
              return (
                <Button className="nav-item" key={`menu-${key}`}>
                  <Link
                    to={`/${key}`}
                    onClick={() => {
                      setActiveMenuItem(`${key}`);
                      setExpandedAccordion("history");
                    }}
                  >
                    <p
                      className={`nav-link ${
                        key === activeMenuItem ? "active" : ""
                      }`}
                      key={`menu-${key}`}
                    >
                      {data[key]}
                    </p>
                  </Link>
                </Button>
              );
            })}
          </Box>

          <Box sx={{ minWidth: 66 }}>
            <FormControl fullWidth className="lang-select">
              <Select
                sx={{
                  ".MuiOutlinedInput-notchedOutline": { borderStyle: "none" },
                  ".MuiSelect-icon": { display: "none" },
                  fontWeight: "bold",
                  color: "#092f63"
                }}
                IconComponent={() => (
                  <Icon
                    fontSize="large"
                    sx={{ color: "#CC9653", marginLeft: "-3rem" }}
                  />
                )}
                labelId="demo-simple-select-label"
                id="demo-simple-select"
                onChange={handleChange}
                value={lang}
                displayEmpty
              >
                {langs.map(lang => (
                  <MenuItem
                    sx={{
                      color: "#092f63",
                      fontWeight: "bold",
                      paddingRight: "1px"
                    }}
                    value={lang}
                    className="lang-dropdown"
                  >
                    {lang}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
          </Box>
        </Toolbar>
      </AppBar>
    </ThemeProvider>
  );
};
export default AppHeader;
