import React, { useRef } from "react";
import emailjs from "@emailjs/browser";
import { TextField, Button } from "@mui/material";
import "./ContactForm.css";

const ContactForm = props => {
  const formData = props.form;
  const form = useRef();

  const sendEmail = e => {
    e.preventDefault();

    emailjs
      .sendForm(
        "service_0anaq2m",
        "template_9i7xmzv",
        form.current,
        "N5zYaee7UCsoouJ2V"
      )
      .then(
        result => {
          console.log(result.text);
        },
        error => {
          console.log(error.text);
        }
      );
  };
  return (
    <>
      <form
        ref={form}
        onSubmit={sendEmail}
        style={{ backgroundColor: "#f1f2f5" }}
      >
        <TextField label={formData.name} name="user_name" fullWidth />
        <TextField label={formData.email} name="user_email" fullWidth />
        <TextField
          label={formData.message}
          name="message"
          fullWidth
          multiline
          rows={5}
        />
        <Button type="submit">{formData.submit}</Button>
        {/*  <label>Name</label>
        <input type="text" name="user_name" />
        <label>Email</label>
        <input type="email" name="user_email" />
        <label>Message</label>
        <textarea name="message" />
        <input type="submit" value="Send" />*/}
      </form>
    </>
  );
};
export default ContactForm;
